.newexpense{
    background-color: rgb(198, 157, 157);
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align:center;
    box-shadow:0 1px 8px rgba(0,0,0,01.25rem)
}

.newexpense_button{
    font:inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border:1px solid #40005d;
    background-color:#40005d ;

}

.newexpense_button:hover,
.newexpense_button:active{
    background-color: #510674;
    border-color: #510674;
}
.newexpense_button.alternative{
    color:#220131;
    border-color: transparent;
    background-color: transparent;
}

.newexpense_button.alternative:hover,
.newexpense_button.alternative:active{
    background-color: #ddb3f8;
}
.newexpense_control label{
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}
.newexpense_control input{
    margin: 1rem;
    font:inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border:1px solid #ccc;
    width: 20rem;
    max-width: 100%;
    
}

.newexpense_actions{
    text-align: right;
}
.btn{
    font:inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border:1px solid #40005d;
    background-color:#40005d ;

}

.btn:hover,
.btn:active{
    background-color: #510674;
    border-color: #510674;
}
.btn.alternative{
    color:#220131;
    border-color: transparent;
    background-color: transparent;
}

.maincontainer{
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #585656;
    
}
.details{
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex:1 1;
}


.date{
    color: white;
   
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #585656;
    width: 7%;
    text-align: center;
}

.price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: rgb(73, 15, 56);
    border:1px solid white;
    padding:0.5rem;
    border-radius: 12px;
}
@media(min-width:580px){
    .maincontainer{
   flex-direction: row;
   align-items: center;
   justify-content: flex-start;
   flex: 1 1;
}
.details{
   font-size: 1.25rem;
}
.price{
   font-size: 1.25rem;
   padding: 0.5rem 1.5rem;
}
}       

.newdate{color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    padding: 0.5rem;

    margin: 1rem 0;
    border-radius: 12px;
    background-color: #1b0a4f;
 
}
.day{
    font-size: larger;
}
