.maincontainer{
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #585656;
    
}
.details{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex:1;
}


.date{
    color: white;
   
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #585656;
    width: 7%;
    text-align: center;
}

.price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: rgb(73, 15, 56);
    border:1px solid white;
    padding:0.5rem;
    border-radius: 12px;
}
@media(min-width:580px){
    .maincontainer{
   flex-direction: row;
   align-items: center;
   justify-content: flex-start;
   flex: 1;
}
.details{
   font-size: 1.25rem;
}
.price{
   font-size: 1.25rem;
   padding: 0.5rem 1.5rem;
}
}       
